.movie {
  display: flex;
  width: 80%;
  margin-top: 5%;
}

.movie img {
  max-width: 450px;
  width: 100%;
  top: -50px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
  width: 70%;
  margin: 0;
}

.movie .movie__title {
  font-weight: 700;
  display: inline-block;
  min-width: 30%;
  margin-bottom: 5px;
  font-size: 25px;
  color: #2c2c2c;
}

.movie__gen {
  display: inline-block;
  background-color: #e6e2f9;
  /* border: 2px solid #555bff; */
  min-width: 25%;
  /* max-width: 50%; */
  margin-top: 5px;
}

.movie .movie__genres {
  list-style: none;
  display: flex;
}

.movie__genres li {
  font-size: 20px;
}
.movie .movie__year {
  font-size: 15px;
  margin-right: 10px;
}

.movie .movie__summary {
  font-size: 20px;
}
.movie .movie__column .a {
  display: flex;
}
